import React from 'react';
import { platform, IOS } from '@vkontakte/vkui';

import PanelSpinner from "@vkontakte/vkui/dist/components/PanelSpinner/PanelSpinner";
import './msgbox.css';
const osName = platform();

class MsgBox extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            id: props.data.id,
            author: props.data.uname,
            text: props.data.message,
            time: '',
            owner: props.owner
        };
    }

    componentDidMount() {
        console.log(this.props);
    }

    render() {


        return (
            <div className='row'>
                <div className={(this.state.author === this.state.owner) ? 'block right' : 'block left'}>
                    <span className='author'>{this.state.author}</span><div className='text'>{this.state.text}</div><span className='time'>{this.state.id}</span>
                </div>
            </div>
        );
    }
}

export default MsgBox;
